'use client';

import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';

export default function PaymentCallbackPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [status, setStatus] = useState<'checking' | 'success' | 'failed'>('checking');
  const [message, setMessage] = useState('Memverifikasi pembayaran...');

  useEffect(() => {
    const resultCode = searchParams.get('resultCode');
    const merchantOrderId = searchParams.get('merchantOrderId');

    if (resultCode === '00') {
      setStatus('success');
      setMessage('Pembayaran berhasil! Anda sekarang dapat melihat pembahasan.');
      
      // Redirect ke review page setelah 3 detik
      setTimeout(() => {
        // Extract resultId dari merchantOrderId
        const parts = merchantOrderId?.split('-');
        if (parts && parts.length >= 2) {
          const resultId = parts[1];
          router.push(`/history/${resultId}/review`);
        } else {
          router.push('/history');
        }
      }, 3000);
    } else {
      setStatus('failed');
      setMessage('Pembayaran gagal atau dibatalkan.');
    }
  }, [searchParams, router]);

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="max-w-md w-full bg-white rounded-lg shadow-lg p-8">
        {status === 'checking' && (
          <div className="text-center">
            <div className="animate-spin rounded-full h-16 w-16 border-b-4 border-blue-600 mx-auto mb-4"></div>
            <h2 className="text-xl font-semibold text-gray-800 mb-2">
              Memverifikasi Pembayaran
            </h2>
            <p className="text-gray-600">{message}</p>
          </div>
        )}

        {status === 'success' && (
          <div className="text-center">
            <div className="text-6xl mb-4">✅</div>
            <h2 className="text-2xl font-bold text-green-600 mb-2">
              Pembayaran Berhasil!
            </h2>
            <p className="text-gray-600 mb-4">{message}</p>
            <div className="animate-pulse text-sm text-gray-500">
              Mengalihkan ke halaman review...
            </div>
          </div>
        )}

        {status === 'failed' && (
          <div className="text-center">
            <div className="text-6xl mb-4">❌</div>
            <h2 className="text-2xl font-bold text-red-600 mb-2">
              Pembayaran Gagal
            </h2>
            <p className="text-gray-600 mb-6">{message}</p>
            <button
              onClick={() => router.push('/history')}
              className="w-full bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700 transition-colors font-medium"
            >
              Kembali ke History
            </button>
          </div>
        )}
      </div>
    </div>
  );
}